import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import BookItem from '../../components/BookItem/BookItem.vue'
import {
	search
} from '../../api/sodu.js'
export default {
	components: {
		uniSearchBar,
		BookItem
	},
	data() {
		return {
			key: '',
			books: []
		};
	},
	methods: {
		async handleSearch(input) {
			try {
				uni.showLoading()
				let res = await search(input.value)
				if (res.code === 0) {
					this.books = []
					setTimeout(() => {
						this.books = res.result
					}, 0)
				} else {
					uni.showToast({
						title: res.message
					})
				}
			} catch (e) {
				//TODO handle the exception
			} finally {
				uni.hideLoading()
			}
		},
		handleClear() {
			this.books = []
		}
	}
}
